import ReactDOM from "react-dom";
import usrs from "./assets/usrs.jpg";
const welcome = "welcome to 30 Days Of React";
const title = "Getting Start React";
const subtitle = " JavaScript Library";
const author = {
  firstName: "Nantaphong",
  lastName: "Boonmanan"
};
const date = "6 July 1997";

const numOne = 3;
const numTwo = 2;

const result = (
  <p>
    {numOne}+{numTwo} = {numTwo}+{numOne}
  </p>
);
const copyRight = "Copyright 2020";
